import React from 'react';
import Description from '@mui/icons-material/Description';
import GroupsIcon from '@mui/icons-material/Groups';
import { styled } from '@mui/system';
import './header.css'
import { Button } from '@mui/material';
import TextEditor from './TextEditor'

const CustomIcon = styled(Description)`
  color: blue;
  font-size: 2.5rem ; 
`;
const Grp = styled(GroupsIcon)`
  color: white;
  font-size: 2rem ;

`;
const shareButtonStyle = {
    position: 'absolute',
    top: '1rem',
    right: '4rem',
    backgroundColor: 'blue',
  };
const logoStyle = {
    position: 'absolute',
    top: '1rem',
    right: '1rem',
    color: 'blue',
    fontSize: '4.5rem',
    borderRadius: '100%',
    backgroundColor: 'red'
    
  };

function Header() {
    const containerStyle = {
      display: 'flex',
      alignItems: 'center',
      padding: '0 2rem',
    };

    const session = {
        user: {
          active: true,
          avatar: 'data:image/png;base64,'
        },
      };
  
    return (
      <div>
        <header>
          <div style={containerStyle}>
            <CustomIcon />
            <h2>Docs</h2>
          </div>
          <div className='flex' style={{ display: 'flex',alignItems: 'center',fontSize: '0.875rem', margin: '0 0.25rem', marginLeft: '0.25rem', height: '2rem',color: 'gray', fontWeight: 600  }}>
            <p className='option'>File</p>
            <p className='option'>Edit</p>
            <p className='option'>View</p>
            <p className='option'>Insert</p>
            <p className='option'>Format</p>
            <p className='option'>Tools</p>
            <p className='option'>Extenstions</p>
          </div>
          <Button style={{color:'lightblue', buttontype:'filled', size:'regular', height: '2.5rem', display: 'flex' , ripple:'light', ...shareButtonStyle }}> <Grp/>  Share</Button>
          {session.user.active && (
              <img src={session.user.avatar} alt="User Avatar" style={{ height: '2rem', cursor: 'pointer', ...logoStyle}} />
            )}
        </header>

        <TextEditor/>
      </div>
    );
  } 

export default Header;
